import Vue from 'vue'
const baseUrl = getApp().globalData.baseUrl

function myRequest(url,method,data){
	/*  */
	const token = uni.getStorageSync("token") || ''
	const headers = {
		"Content-Type": "application/json; charset=utf-8",
		'Authorization': token
	}
	
	return new Promise((resolve,reject) => {
		uni.request({
			url: baseUrl + url,
			method: method,
			data: data,
			header: headers,
			timeout: 10000,
			success(result) 
			{
				// console.log(result.header);
				/*  */
				if(result.data.code === 200)
				{
					resolve(result.data)
				}
				/*  */
				else
				{
					console.log("")
					console.log(result.data)
					/* token */
					if(result.data.code === 103){
						// token
						if(token === uni.getStorageSync("token")){
							uni.clearStorageSync("token")
							uni.reLaunch({
								url: "/pages/Login/Login",
								success: () => {
									Vue.prototype.gToastError(result.data.msg)
								}
							})
						}
						// tokentoken
						else{
							resolve(myRequest(url,method,data))
						}
					}
					else if(result.data.msg){
						Vue.prototype.gToastError(result.data.msg)
					}
					else{
						Vue.prototype.gToastError("")
					}
					reject(result.data)
				}
			},
			fail(err) 
			{
				console.log("")
				console.log(err.data)
				Vue.prototype.gToastError("")
				reject(err)
			},
			complete(result) {
				/* token,token */
				if(result.header.Authorization){
					console.log(result.header.Authorization);
					uni.setStorageSync("token",result.header.Authorization)
				}
			}
		})
	})
}
export default myRequest