import request from "./request.js"

/* 
	description: 
	input: 
			phone: String,
			type: String,registerchangePasswordbindPhone
	return: null
*/
const sendCode = (data) => request(`/sms/code`,"POST",data)

/* 
	description: 
	input: 
				phone: String,
				password: String,
				code: String,
	return: 
				token: String
				userInfo: Object,
*/
const Register = (data) => request("/register/phone","POST",data)

/* 
	description: 
	input: 
				newPassword: String,
				code: String,
	return: null
*/
const ChangePassword = (data) => request(`/changePassword/phone/${data.phone} `,"POST",data)

/* 
	description: 
	input: 
				phone: String,
				password: String,
	return: 
				token: String
				userInfo: Object,
*/
const Login = (data) => request("/login/phone","POST",data)

/* 
	description: 
	input: 
				code: String,
	return: 
				token: String
				userInfo: Object,
*/
const WXLogin = (data) => request("/login/wechat","POST",data)

/*  */
const loginOut = () => request("/logout ","GET",{})

export {
	sendCode,
	Register,
	ChangePassword,
	Login,
	WXLogin,
  loginOut
}